.lmj-plant-item {
	margin: rem(10);
	display: flex;
	align-items: flex-start;
	justify-content: center;
	flex-direction: column;
	text-transform: capitalize;
	position: relative;
	border-radius: rem(10);
	&-cover {
		position: relative;
		height: rem(340);
		max-width: rem(340);
		background: #FF6060;
		border-radius: rem(10);
		object-fit: cover;
		z-index: 100;
	}
	&-title{
		width: rem(300);
		height: rem(32);
		padding: rem(5) 0 rem(5) rem(5);
		position: absolute;
		bottom: rem(20);
		left: rem(15);
		border-radius: rem(10);
		color: white;
		font-family: 'Montserrat';
		font-size: rem(18);
		font-weight: 500;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		text-shadow: 2px 2px 3px black;
		z-index: 300;
	}
	&-background{
		position: absolute;
		max-width: rem(340);
		height: rem(340);
		background: black;
		opacity: 0.2;
		z-index: 200;
		border-radius: rem(10);
	}
}
@media screen and (min-width:300px) and (max-width:475px){
	.lmj-plant-item {
		margin: rem(10);
		display: flex;
		align-items: flex-start;
		justify-content: center;
		flex-direction: column;
		text-transform: capitalize;
		position: relative;
		border-radius: rem(10);
		&-cover {
			position: relative;
			height: rem(250);
			max-width: rem(330);
			background: #FF6060;
			border-radius: rem(10);
			object-fit: cover;
			z-index: 100;
		}
		&-title{
			width: rem(300);
			height: rem(32);
			padding: rem(5) 0 rem(5) rem(5);
			position: absolute;
			bottom: rem(20);
			left: rem(15);
			border-radius: rem(10);
			color: white;
			font-family: 'Montserrat';
			font-size: rem(18);
			font-weight: 500;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			text-shadow: 2px 2px 3px black;
			z-index: 300;
		}
		&-background{
			position: absolute;
			max-width: rem(330);
			height: rem(250);
			background: black;
			opacity: 0.2;
			z-index: 200;
			border-radius: rem(10);
		}
	}
}